'use client';

import { useState, useCallback } from 'react';
import { CreditMovement } from '@/components/CreditSystem';

export const useCreditSystem = (initialCredits: number = 12) => {
  const [credits, setCredits] = useState(initialCredits);
  const [showLowCreditWarning, setShowLowCreditWarning] = useState(false);
  const [movements, setMovements] = useState<CreditMovement[]>([
    {
      id: '1',
      type: 'gain',
      amount: 2,
      reason: 'Bonus de progression - Quiz MathÃ©matiques terminÃ©',
      timestamp: new Date(Date.now() - 2 * 24 * 60 * 60 * 1000)
    },
    {
      id: '2',
      type: 'spend',
      amount: 1,
      reason: 'Cours "Physique pour IngÃ©nieurs" dÃ©bloquÃ©',
      timestamp: new Date(Date.now() - 1 * 24 * 60 * 60 * 1000),
      courseId: 'physics-eng'
    },
    {
      id: '3',
      type: 'gain',
      amount: 1,
      reason: 'Connexion quotidienne - Bonus fidÃ©litÃ©',
      timestamp: new Date(Date.now() - 3 * 60 * 60 * 1000)
    }
  ]);
  
  const [showAnimation, setShowAnimation] = useState<{ type: 'gain' | 'spend'; amount: number } | null>(null);
  const [showModal, setShowModal] = useState(false);

  // DÃ©penser des crÃ©dits
  const spendCredits = useCallback((amount: number, reason: string, courseId?: string) => {
    if (credits < amount) {
      return false; // Pas assez de crÃ©dits
    }

    setCredits(prev => prev - amount);
    
    const newMovement: CreditMovement = {
      id: Date.now().toString(),
      type: 'spend',
      amount,
      reason,
      timestamp: new Date(),
      courseId
    };
    
    setMovements(prev => [newMovement, ...prev]);
    setShowAnimation({ type: 'spend', amount });
    
    // VÃ©rifier si les crÃ©dits sont faibles aprÃ¨s la dÃ©pense
    const newCreditAmount = credits - amount;
    if (newCreditAmount <= 5 && newCreditAmount > 0) {
      setTimeout(() => {
        setShowLowCreditWarning(true);
      }, 2000); // Afficher aprÃ¨s l'animation de dÃ©pense
    }
    
    // Masquer l'animation aprÃ¨s un dÃ©lai
    setTimeout(() => setShowAnimation(null), 1500);
    
    return true; // SuccÃ¨s
  }, [credits]);

  // Gagner des crÃ©dits
  const gainCredits = useCallback((amount: number, reason: string) => {
    setCredits(prev => prev + amount);
    
    const newMovement: CreditMovement = {
      id: Date.now().toString(),
      type: 'gain',
      amount,
      reason,
      timestamp: new Date()
    };
    
    setMovements(prev => [newMovement, ...prev]);
    setShowAnimation({ type: 'gain', amount });
    
    // Masquer l'animation aprÃ¨s un dÃ©lai
    setTimeout(() => setShowAnimation(null), 1500);
  }, []);

  // GÃ©nÃ©rer des suggestions personnalisÃ©es
  const getSuggestions = useCallback(() => {
    const suggestions = [];
    
    if (credits >= 3) {
      suggestions.push("Avec tes 3+ crÃ©dits, tu peux dÃ©bloquer une sÃ©rie complÃ¨te en Sciences pour construire une base solide.");
    } else if (credits >= 2) {
      suggestions.push("Tes 2 crÃ©dits te permettent de choisir 2 cours complÃ©mentaires pour renforcer tes compÃ©tences.");
    } else if (credits === 1) {
      suggestions.push("Avec ton crÃ©dit restant, concentre-toi sur le cours qui t'apportera le plus de valeur immÃ©diate.");
    } else {
      suggestions.push("Termine tes cours actuels pour gagner de nouveaux crÃ©dits et dÃ©bloquer plus de contenu !");
    }

    // Suggestions basÃ©es sur l'historique
    const recentSpends = movements.filter(m => m.type === 'spend').slice(0, 3);
    if (recentSpends.length > 0) {
      suggestions.push("BasÃ© sur tes derniers choix, nous recommandons de continuer dans la mÃªme thÃ©matique pour approfondir tes connaissances.");
    }

    return suggestions;
  }, [credits, movements]);

  return {
    credits,
    movements,
    showAnimation,
    showModal,
    setShowModal,
    showLowCreditWarning,
    setShowLowCreditWarning,
    spendCredits,
    gainCredits,
    getSuggestions,
    canAfford: (amount: number) => credits >= amount
  };
};

