// Calcule la valeur ajoutÃ©e du pack
export const calculatePackValue = (pack: CoursePack) => {
  const baseValue = pack.originalCreditCost;
  const actualCost = pack.creditCost;
  const savings = baseValue - actualCost;
  
  return {
    savings,
    percentageSave: Math.round((savings / baseValue) * 100),
    bonusContent: pack.features.length - pack.courses.length, // Nombre de features bonus
    valueProposition: `${savings} crÃ©dits de contenu bonus inclus`
  };
};

// DÃ©termine le niveau de recommandation
export const getRecommendationLevel = (lesson: Lesson) => {
  // LeÃ§ons de difficultÃ© 'hard' â†’ forte recommandation pour le pack
  if (lesson.difficulty === 'hard') {
    return {
      primary: 'pack',
      message: 'Ce concept avancÃ© nÃ©cessite une approche complÃ¨te pour Ãªtre maÃ®trisÃ©.'
    };
  }
  
  // LeÃ§ons 'medium' â†’ recommandation modÃ©rÃ©e pour le cours
  if (lesson.difficulty === 'medium') {
    return {
      primary: 'course',
      message: 'Cette leÃ§on fait partie d\'un ensemble cohÃ©rent Ã  Ã©tudier.'
    };
  }
  
  // LeÃ§ons 'easy' â†’ recommandation lÃ©gÃ¨re
  return {
    primary: 'course',
    message: 'MÃªme les bases sont mieux comprises dans leur contexte global.'
  };
};

// Recommandations pour les cours (similaire aux leÃ§ons mais spÃ©cifique aux cours)
export const getCourseRecommendations = (course: Course) => {
  const coursePacks = getCoursePacks();
  
  // Mapping cours -> pack recommandÃ© basÃ© sur la matiÃ¨re et le niveau
  const getRecommendedPack = (): CoursePack => {
    // MathÃ©matiques et Statistiques â†’ Pack MathÃ©matiques AvancÃ©es
    if (course.id === '1' || course.id === '4' || course.title.toLowerCase().includes('math')) {
      return coursePacks.find(pack => pack.id === 'pack-math-advanced') || coursePacks[0];
    }
    
    // Physique et Chimie â†’ Pack Sciences Exactes  
    if (course.id === '2' || course.id === '3' || course.title.toLowerCase().includes('physique') || course.title.toLowerCase().includes('chimie')) {
      return coursePacks.find(pack => pack.id === 'pack-chimie-complete') || coursePacks[0];
    }
    
    // Ã‰conomie â†’ Pack RentrÃ©e
    if (course.id === '5' || course.faculty === 'Ã‰conomie' || course.title.toLowerCase().includes('Ã©conomie')) {
      return coursePacks.find(pack => pack.id === 'pack-rentree') || coursePacks[0];
    }
    
    // Par dÃ©faut â†’ Pack Excellence
    return coursePacks.find(pack => pack.id === 'pack-excellence') || coursePacks[0];
  };

  // Pack alternatif pour donner le choix
  const getAlternativePack = (mainPack: CoursePack): CoursePack => {
    // Si le pack principal n'est pas Excellence, proposer Excellence
    if (mainPack.id !== 'pack-excellence') {
      return coursePacks.find(pack => pack.id === 'pack-excellence') || coursePacks[0];
    }
    // Sinon proposer le pack MathÃ©matiques AvancÃ©es
    return coursePacks.find(pack => pack.id === 'pack-math-advanced') || coursePacks[0];
  };

  const recommendedPack = getRecommendedPack();
  const alternativePack = getAlternativePack(recommendedPack);

  return {
    recommendedPack,
    alternativePack
  };
};

